header {
  position: relative; }

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 5; }

.hamburger {
  display: none; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  &_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300;
    text-decoration: none; }

  &_item {
    border-right: 2px dotted black;
    padding-right: 28px;
    &:last-child {
      border-right: none; } } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  &_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  &_official {
    margin-top: 6px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400; }

  &_call {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }

  &_phone {
    display: block;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px;
    text-decoration: none; }

  &_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, $main_color 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }


.promo {
  min-height: 800px;
  background: url(../img/uber_bg.jpg) center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  &_header {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 60px;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  &_subheader {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 36px;
    line-height: 36px;
    margin: 0;
    margin-top: 22px;
    text-align: center; }

  &_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  &_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, $main_color 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }

.reasons {
  padding: 71px 0 65px 0;
  &_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; }
  &_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: $main_color;
    border-radius: 100%; }
  &_descr {
    width: 343px; }
  &_subtitle {
    line-height: 21px;
    color: $main_color;
    font-size: 17px;
    font-weight: 400; }
  &_text {
    color: #202020;
    font-size: 15px;
    font-weight: 300;
    line-height: 21px; } }

.label {
  position: relative;
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center;
  z-index: 2;
  &_white {
    color: black;
    background-color: #fff; } }

.title {
  position: relative;
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 37px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  z-index: 2;
  &_white {
    color: #FFFFFF; } }

.subtitle {
  position: relative;
  font-size: 17px;
  line-height: 21px;
  align-items: center;
  text-align: center;
  margin-top: 11px;
  z-index: 2;
  &_white {
    color: #FFFFFF; } }



.mobile {
  min-height: 864px;
  padding: 48px 0 80px 0;
  background: url(../img/mobile_bg.jpg) center (center / cover) no-repeat;
  .row {
    margin-top: 30px; }

  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 88px;
    &_1 {
      background: url(../img/mobile_screen_1.jpg) center (center / cover) no-repeat; }
    &_2 {
      background: url(../img/mobile_screen_2.jpg) center (center / cover) no-repeat; }
    &_3 {
      background: url(../img/mobile_screen_3.jpg) center (center / cover) no-repeat; }
    &_4 {
      background: url(../img/mobile_screen_4.jpg) center (center / cover) no-repeat; }
    &_5 {
      background: url(../img/mobile_screen_5.jpg) center (center / cover) no-repeat; }
    &_6 {
      background: url(../img/mobile_screen_6.jpg) center (center / cover) no-repeat; }
    &_subtitle {
      font-size: 24px;
      line-height: 31px;
      text-align: center;
      text-transform: uppercase;
      color: #FFFFFF; }
    &_plus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      bottom: 35px;
      left: 50%;
      transform: translateX(-50%);
      width: 29px;
      height: 29px;
      background: #000000;
      margin: 0 auto;
      border-radius: 100%;
      cursor: pointer;
      &:before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: $main_color;
        transition: 0.5s all; }
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        width: 2px;
        height: 14px;
        background-color: $main_color;
        transition: 0.5s all; }
      &:hover {
        &:before, &:after {
          background-color: $second_color; } } } } }

.choice {
  position: relative;
  // height: 898px
  background: url(../img/choice_bg.png) center (center / cover) no-repeat;
  padding: 57px 0 137px 0;
  &_img {
    position: relative;
    display: block;
    margin: 57px auto 0 auto;
    z-index: 2; }
  &_descr {
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    text-align: center;
    color: #222222; }
  .black {
    position: absolute;
    top: 0;
    left: 0;
    height: 502px;
    background: #070716;
    width: 100%;
    z-index: 1; } }

.require {
  padding: 68px 0 87px 0;
  &_block {
    margin: 33px 0;
    &_nmp {
      margin-bottom: 0px; } }
  &_title {
    font-size: 22px;
    line-height: 24px;
    align-items: center;
    color: #1CA8C3;
    &_coral {
      color: $second_color; } }
  &_list {
    list-style-type: none;
    padding-left: 52px;
    margin-top: 20px;
    li {
      position: relative;
      font-weight: 300;
      font-size: 17px;
      line-height: 24px;
      color: #222222;
      margin-bottom: 10px;
      &:before {
        content: '';
        display: block;
        position: absolute;
        left: -34px;
        top: 3px;
        width: 21px;
        height: 20px;
        background: url(../icons/check_blue.png) center (center / cover) no-repeat; } }
    &_coral {
      li {
        &:before {
          background: url(../icons/check_red.png) center (center / cover) no-repeat; } } } }
  &_descr {
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    color: #1CA8C3; } }

.world {
  min-height: 720px;
  padding: 48px 0 22px 0;
  background: url(../img/world_bg.jpg) center (center / cover) no-repeat;
  &_link {
    display: block;
    margin: 456.5px auto 0 auto;
    font-size: 15px;
    line-height: 68px;
    text-align: center;
    text-transform: uppercase;
    color: #1FBAD6;
    text-decoration: none; } }

footer {
  padding: 11px 22px 39px 22px;
  .footer {
    &_divider {
      width: 299px;
      height: 4px;
      background: #1FBAD6;
      margin: 0 auto; }
    &_wrapper {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      padding-top: 33px; }
    &_wrapper > div {
      width: 33%;
      &:nth-child(3) {
        display: flex;
        justify-content: flex-end; } }
    &_social {
      display: flex;
      justify-content: space-between;
      width: 160px;
      &_item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 32px;
        height: 32px;
        background: #070716;
        border-radius: 100%;
        i {
          color: white; } } }
    &_links {
      width: 390px;
      margin: 0 auto;
      &_main {
        display: flex;
        justify-content: space-between;
        // padding: 0 10px
        a {
          color: #070716;
          font-size: 15px;
          font-weight: 300;
          text-transform: uppercase;
          text-decoration: none; } }
      &_sub {
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
        a {
          color: #999999;
          font-size: 11px;
          font-weight: 300;
          text-transform: uppercase;
          text-decoration: none; } }
      &_lang {
        display: block;
        margin: 0 auto;
        font-size: 11px;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase;
        color: #1FBAD6;
        margin-top: 19px;
        text-decoration: none; } } } }

.xs-visible {
  display: none; }
